import React, { useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import Select, { createFilter } from 'react-select';
import Modelbox from '../ModelBox/ModelBox';
import "./Selectbox.css"


function Selectbox({type, options, title}) {
    const styles = {
  option: (provided, state) => ({
    ...provided,
    textAlign: 'left', // Align options to the left
    fontFamily: "Inter",
  }),
  control: (provided) => ({
    ...provided,
    textAlign: 'left',
    borderRadius: '12px',
    fontFamily: "Inter",
    fontWeight: "500",
    height: "45px",
  }),
  
  
  menu: (provided, state) => ({
    ...provided,
    borderRadius: '10px', // Set your desired border radius here
    marginTop: 0,
    paddingBottom: "5px",
  }),
};
const filterOption = createFilter({
    matchFrom: 'start', // Match from the beginning of the label
    allowEmpty: true, // Allow empty search string
    trim: false, // Don't trim whitespace from input
    filterOption: (option, inputValue) =>
      option.label.toLowerCase().startsWith(inputValue.toLowerCase()),
  });
  const [value, setValue] = useState("all");
  const location = useLocation();
  const navigate = useNavigate();
  const queryParams = new URLSearchParams(location.search);
  
  function handleChange(newValue){
    // update query parameters
    setValue(newValue.value);
    
    queryParams.set(type, newValue.value);

    // create new search string and navigate to it
    const newSearch = `?${queryParams.toString()}`;
    navigate({ search: newSearch });
    
    
    
  }

  const returnModelBox = () => {
    if (type == "marka") {
      if (value !== "all") {
        return ( <Modelbox disabled={false} type={value}/> )
      }
      else {
        return ( <Modelbox disabled={true}/> )
      }
    }
    
  }


  // Example usage




  return (
    <div className='selectbox-container'>
      <div className='selectbox'  style={{width: (type == "marka" ? "50%" : "100%"),}}
      >
        <Select
            
            className="basic-single"
            classNamePrefix="select"
            defaultValue={null}
            filterOption={filterOption}
            isClearable={false}
            isSearchable={true}
            name="color"
            options={options}
            onChange={handleChange}
            styles={styles}
            placeholder={title}
            components={{
              IndicatorSeparator: () => null
            }}
            
          />
      </div>
        
        {returnModelBox()}
    
    </div>
    
  );
};

export default Selectbox;
