
import React, { useEffect, useState } from "react";
import { useLocation } from 'react-router-dom';


function SearchResults() {
    const {state} = useLocation();
    const { marka, model, city } = state; // Read values passed on state
    const [mymarka, setMymarka] = useState(marka)
    const [mymodel, setMymodel] = useState(model);
    const [mycity, setMycity] = useState(city);
    useEffect(() => {
      console.log(marka);
      if (marka == null) {
        setMymarka("all");
        setMymodel("all")
      }
      if (city == null) {
        setMycity("all");
      }
    }, [])
    
  return (
    <div className="App">
      <h1>Search results: {mymarka} {mymodel} {mycity}</h1>
      <button onClick={() => { console.log(marka) }}>Get results</button>
    </div>
  );
}

export default SearchResults;
